@import url(https://fonts.googleapis.com/css?family=VT323);

.piece {
  display: block;
  height: 100%;
  left: 0;
  top: 0;
  width: 100%;
}
.noclick {
  pointer-events: none;
}
.frame {
  background-color: #051805;
  border-radius: 2rem;
  border: 1px solid;
  border-bottom-color: #121212;
  border-left-color: #080808;
  border-right-color: #080808;
  border-top-color: #020202;
  box-shadow: inset 0 0 18px black, inset 0 0 3rem black, 0 0 10rem black;
  height: 80px;
  position: absolute;
  top: 2%;
  left: 2%;
  width: 96%;
  pointer-events: none;
}
.emblem {
  display: none;
  position: absolute;
  left: 50%;
  bottom: 0;
  font-size: 2rem;
  height: 3rem;
  width: 3rem;
  text-align: center;
  color: white;
  background: -webkit-linear-gradient(#ffffff, #555555);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0.1rem 0rem rgba(0, 0, 0, 0.4), 0 0 2rem rgba(0, 0, 0, 0.8);
  transform: translate(-50%);
}
.output {
  animation: crt-output 10ms infinite;
  background-color: #031e11;
  overflow: scroll;
  position: absolute;
  padding: 3rem 2rem;
  pointer-events: auto;
  text-shadow: 0rem 0.2rem 1rem #0c7b46;
  z-index: -1;
}
@keyframes crt-output {
  0% {
    opacity: 0.9;
  }
  50% {
    opacity: 1;
  }
}
.scanlines {
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0) 50%, rgba(0, 0, 0, 0.2) 70%, rgba(0, 0, 0, 0.6));
  background-size: 100% 0.3rem;
  border-radius: 2rem;
  position: absolute;
}
.glow222 {
  animation: crt-glow 1s infinite;
  background: radial-gradient(circle at center, #1bd459 0%, rgba(27, 212, 89, 0.88) 58%, rgba(21, 235, 92, 0.57) 80%, rgba(19, 94, 29, 0.27) 93%, rgba(10, 23, 12, 0) 100%);
  opacity: 0.30;
  pointer-events: none;
}
@keyframes crt-glow {
  0% {
    opacity: 0.5;
  }
  50% {
    opacity: 0.3;
  }
}
::-moz-selection {
  background-color: #14fdce;
  color: #031e11;
}
::selection {
  background-color: #14fdce;
  color: #031e11;
}
@font-face {
  font-family: 'uuxsl';
  src: url('https://i.icomoon.io/public/188ac2dd2d/VOX/uuxsl.eot');
}
@font-face {
  font-family: 'uuxsl';
  src: url(data:application/x-font-ttf;charset=utf-8;base64,) format('truetype'), url(data:application/font-woff;charset=utf-8;base64,) format('woff');
  font-weight: normal;
  font-style: normal;
}
::selection [class^="icon-"],
::selection [class*=" icon-"] {
  font-family: 'uuxsl';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
::selection .icon-information:before {
  content: "\e626";
}
::selection .noisy {
  background-image: url(data:image/png;base64,);
}



